.Body-div {
    display: flex;
    margin: auto;
    width: 90%;
    flex-wrap: wrap;
    justify-content: center;
}

.card {
    display: flex;
    flex: 1 1 100%;
    flex-direction: row;
    margin: 10px;
}

.card:hover {
    -webkit-transform: scale(1.01);
    transform: scale(1.01);
}

.card-image {
    width: 100%;
    height: 300px;
    min-width: 300px;
}

.is-4by3 {
    height: inherit;
}